.fadeOut {
  opacity: 0;
  width: 0;
  height: 0;
  font-size: 0vmax;
  transition: width 1s, height 1s, opacity 1s, font-size 1s;
  font-family: "Rubik Mono One", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji",
    sans-serif;
  font-weight: 500;
  position: absolute;
  z-index: 999;
  text-align: center;
  left: 25%;
  top: 50%;
}

.fadeInTwo {
